.container {
  padding: 15px 20px;
  border-bottom: 1px solid var(--border-primary);
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  grid-area: top; }
.description {
  text-transform: uppercase;
  font-size: 0.8em;
  font-weight: 700;
  color: var(--text-faded);
  margin-right: auto; }
.search {
  display: none; }

@media (min-width: 756px) {
  .search {
    display: inherit; } }
