@import '../../index';
.container {
  position: sticky;
  z-index: 1000;
  top: 0;
  background: var(--background-primary);
  grid-area: nav; }
.parent {
  display: flex;
  justify-content: space-between; }
.links {
  @extend %list;
  width: 70%;
  height: calc(100vh - 56px);
  position: absolute;
  background: var(--background-primary);
  transition: 0.2s;
  transform: translateX(-100%);
  padding: 0 10px; }

.top {
  display: flex;
  justify-content: space-between;
  box-shadow: 0 0 8px 6px var(--border-primary);
  padding: 10px 20px; }
.toggle {
  font-size: 2em;
  margin: 0; }
.toggleCheckbox {
  appearance: none;
  display: none;
  &:checked ~ .links {
    transform: translateX(0); } }

.link {
  color: inherit;
  padding: 10px; }
.item {
  @extend %title;
  padding-right: 10px;
  position: relative;
  display: flex;
  flex-flow: column;
  &:hover > .nested {
    display: initial; }
  .nested {
    @extend %list;
    margin-left: 20px;
    white-space: nowrap; } }
.nested {
  left: 100%;
  top: 0;
  z-index: 1;
  margin-left: 10px;
  display: none;
  .item {
    background: var(--background-pink);
    color: #ffffff; } }

@media (min-width: 756px) {
  .container {
    order: initial;
    display: flex;
    justify-content: center; }
  .links {
    height: initial;
    width: initial;
    position: initial;
    display: flex;
    transform: translateX(0);
    &:hover > .item {
      opacity: 0.5; }
    & > .item:not(:last-child) {
      padding-right: 20px; } }
  .flat {
    & > .nested {
      position: absolute;
      top: 100%;
      left: 0; } }
  .item {
    &:hover {
      opacity: 1!important; }
    &:hover > .nested {
      display: initial; } }
  .nested {
    width: initial;
    min-width: 100%;
    display: none;
    position: absolute;
    border-left: 5px solid white;
    margin-left: 0!important;
    height: initial!important;
    &:hover {
      height: initial!important; }
    .item {
      margin-top: 5px;
      padding: 10px 20px; }
    &:hover > .item {
      opacity: 0.5; } }

  .top, .toggleCheckbox {
    display: none; } }
