.container {
  display: flex;
  min-width: 100%;
  height: 100%;
  background: no-repeat center;
  background-size: cover;
  order: 0;
  position: relative;
  align-items: center;
  justify-content: center; }
.box {
  text-align: center;
  background: var(--background-primary);
  padding: 30px;
  position: absolute;
  min-width: 300px;
  outline: 50px solid rgba(255,255,255,0.5); }

