@import "../../index";
.container {
  @extend %section;
  grid-area: tags; }
.list {
  @extend %list;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around; }
.tag {
  border: 3px solid var(--border-pink);
  border-radius: 6px;
  margin-right: 20px;
  margin-bottom: 10px;
  //font-size: 0.8em
  transition: 0.15s;
  cursor: pointer;
  &:hover {
    background: var(--border-pink);
    a {
      font-weight: 700;
      color: #ffffff;
      text-decoration: none; } }
  a {
    padding: 4px 6px;
    transition: inherit;
    color: var(--text-faded);
    text-transform: uppercase; } }
